/**
 * Tiny media plugin property setter class for image and embed.
 *
 * @module      tiny_media/propertysetter
 * @copyright   2024 Stevani Andolo <stevani@hotmail.com.au>
 * @license     http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */

export class PropertySetter {

    constructor(data) {
        // Creating dynamic properties based on the data param.
        for (const property in data) {
            const value = data[property];
            if (typeof value !== 'function') {
                this[property] = data[property];
            }
        }
    }
}
